import React from 'react'
import { useSelector } from 'react-redux'
import useMovieTrailerForBg from './customHooks/useMovieTrailerForBg';

const VideoBackground = ({movieId}) => {
  const trailerId=useSelector(store=>store.movies?.trailerVideo)
  
  useMovieTrailerForBg(movieId);
  return (
    <div className='w-screen ' >
      <iframe
      className='w-screen aspect-video '
      src={"https://www.youtube.com/embed/"+ trailerId?.key+"?si="+ trailerId?.key+ "?&autoplay=1&mute=1" }  
      title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" ></iframe>
    </div>
  )
}

export default VideoBackground